import { Block } from '@ali/4ever-cangjie';
import {
  JsonML,
  getTagName,
  getAttributes,
} from '@ali/4ever-utils';
import { ELEMENT_TYPE, key } from '../../utils/meta';
import Model from '../models';

export default {
  name: key,

  match(node: JsonML): boolean {
    return getTagName(node) === ELEMENT_TYPE;
  },

  convert(node: JsonML): Block {
    return Model.create({
      data: {
        checked: false,
        ...(getAttributes(node) || {}),
      },
    });
  },
};
